@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@200;400;600&display=swap);
.temperature-container, .widget, .outside-info {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.widget {
  padding: 15px 0;
  position: relative;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  font-family: 'Nunito', sans-serif;
  font-size: 20px;
  color: white;
  background-color: rgb(39, 39, 39);
  border-radius: 5px;
  max-width: 500px;
  min-width: 280px;
  height: 360px;
}

/* reusable classes */
  .thin-small-font {
    font-weight: 200;
    font-size: 18px;
  }
  .loading {
    color: rgb(138, 138, 138);
    font-size: 20px;
  }

.outside-description {
  margin-top: -20px;
}

.update {
  position: absolute;
  right: 4%;
  top: 20px;
  cursor: pointer;
}

.upd-animation {
  -webkit-animation: .5s linear infinite rotate;
          animation: .5s linear infinite rotate; 
}
@-webkit-keyframes rotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@media screen and (max-width: 360px) {
  .update {
    right: 2%;
  }
}
.button, .location-input {
  background-color:  rgb(39, 39, 39);
  color: white;
  font-size: medium;
  border: none;
}

.change-location-form, .curr-location { 
  display: flex;
  justify-content: center;
}

.change-location-form {
  -webkit-user-select: text;
      -ms-user-select: text;
          user-select: text;
  width: 80%;
  height: 31px;
  padding-bottom: 10px;
}

.curr-location {
  display: flex;
  justify-content: center;
  cursor: pointer;
  font-size: 30px;
}

.button {
  text-align: center;
  width: 60px;
  padding: 0;
  cursor: pointer;
  border: 1px white solid;
  border-radius: 4px;
  font-weight: 200;
}

.location-input {
  outline: none;
  margin-right: 5px;
}

.city-inp {
  width: 50%;
}

.coutry-inp {
  width: 15%;
}

/* input animation */
.location-input {
  background-image: 
    linear-gradient(0deg,white 2px,rgba(0, 0, 0, 0) 0),
    linear-gradient(0deg,gray 2px,rgba(0, 0, 0, 0) 0);

  background-size: 0% 100%, 100% 100%;
  background-repeat: no-repeat;
  transition: all .5s ease;
}

input:focus {
  background-size: 100% 100%, 100% 100%;
}
.current {
  font-size: 70px;
  font-weight: 600;
}

.feels-like {
  background-color: rgba(61, 61, 61, 0.795);
  border-radius: 5px;
  margin-top: -10px;
  width: 160px;
  text-align: center;
}
.secondary-info-container, .wrapper {
  display: flex;
}

.secondary-info-container {
  width: 85%;
  justify-content: space-between;
}

/* .wind, .humidity, .pressure, .visibility {

}  */

.wrapper {
  display: flex;
}

.metric {
  padding-top: 5px;
  font-size: 12px;
}

.value { 
  font-size: 30px;
}

@media screen and (max-width: 360px) {
  .secondary-info-container {
    width: 95%;
  }
}
#modal-root {
  position: relative;
  z-index: 999;
}

.alert-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 5px;
  position: absolute;
  height: 92%;
  width: 100%;
}

.alert {
  background-color: rgba(231, 230, 230, 0.98);
  border: #ff7142  1px solid;
  padding: 10px 10px;
  position: relative;
  min-width: 50%;
  max-width: 70%;
  min-height: 90px;
  /* background-color: #272727; */
  border-radius: 5px;
}

.header {
  display: flex;
  justify-content: flex-end;
  padding-bottom: 5px;
}
.close-button {
  cursor: pointer;
  padding: 0;
  height: 22px;
  width: 22px;
  border: none;
  background-color: rgba(231, 230, 230, 0);
}
.close-button > img {
  height: 20px;
  width: 20px;
}

.body {
  padding: 0 5%;
}
.content {
  color: black;
  text-align: center;
  padding-bottom: 15px;
}


